/**
 * hook for get user member list
 *
 * @param type is all member list or direct member list
 * @param top page query page size
 * @returns page query result
 */
export function useMemberListByDepartment(
    type: MemberType,
    isRootDepartment: boolean,
    departmentId?: string,
    top?: number,
    skipToken?: string
): Omit<UseInfiniteQueryResult<UserView[], RequestError>, 'data'> & UseReactPageQueryResult<UserView[]> {
    const isAllMember = type === 'AllMember';
    const func = isAllMember
        ? SERVICE_CLIENT.fetchGetAllUserMembersByDepartment
        : SERVICE_CLIENT.fetchGetDirectUserMembersByDepartment;

    const params = {
        departmentId,
        isRootDepartment,
        top,
        skipToken,
    };
    return useReactPageQuery(func, params, { enabled: !!departmentId });
}

// export type MemberType = 'AllMember' | 'DirectMember';

export enum MemberType {
    AllMember = 'AllMember',
    DirectMember = 'DirectMember',
}
